import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { GenericDialogData } from 'src/app/models/GenericDialogData';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-testcase-dialog',
  templateUrl: './testcase-dialog.component.html',
  styleUrls: ['./testcase-dialog.component.scss']
})
export class TestcaseDialogComponent implements OnInit {

  title = '';
  code = '';

  constructor(public dialogRef: MatDialogRef<TestcaseDialogComponent>,
              @Inject(MAT_DIALOG_DATA) public data: GenericDialogData, public snackBar: MatSnackBar) {
                // dialogRef.updateSize('80%', '500px');
                this.title = data.title;
                this.code = data.message;
  }

  ngOnInit() {
  }

  copy() {
    const selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = this.code;
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);

    this.snackBar.open('Content is copied', 'Noted', {
      duration: 2000,
    });
  }

  onOK() {
    this.dialogRef.close();
  }

}
